*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  }
  html,body
  {
  height:100%;
  width:100%;
  }
    nav h1 {
      font-family: 'Playwrite CO', sans-serif;
      font-size: 30px;
    }
    
    nav h4 {
      font-family: 'Poppins', sans-serif;
      font-size: 18px;
      font-weight: 500;
    }
    
    .voteup-text {
      font-family: 'Playwrite CO', sans-serif;
      font-size: 24px;
      font-weight: bold;
      color: #333;
    
    }
    #center-part1 h1 {
      font-family: 'china', sans-serif;
      font-size: 3vw;
      line-height: 1.2;
    }
    
    #center-part1 p {
      font-family: 'Noto Serif', serif;
      font-size: 1.2vw;
      width: 95%;
      margin-top: 20px;
      margin-bottom: 20px;
      font-weight: 300;
      font-style: italic;
    }
    
    span {
      font-family: 'Playwrite ES Deco', cursive;
      font-weight: 400;
      font-style: normal;
    }
    
    nav {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1px 50px;
    }
    
    #part2 {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 30px;
      text-decoration: none;
    }
    
    .logo {
      height: 40px;
      width: auto;
    }
    
    nav button {
      padding: 15px 28px;
      font-size: 16px;
      border-radius: 8px;
      background-color: transparent;
      font-weight: 600;
      border: 1px solid #000;
      text-decoration: none;
    }
    
    .center {
      padding: 30px 40px;
      height: 80%;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      gap: 30px;
    }
    
    #center-part1 {
      width: 44%;
    }
    
    #center-part2 {
      width: 50%;
      position: relative;
    }
    
    #i {
      height: 100%;
      width: 100%;
      position: absolute;
      right: 0;
    }
    
    .services p {
      width: 40%;
      font-size: 16px;
      font-weight: 300;
    }
    
    .container {
      height: 86%;
      width: 100%;
      padding: 20px 80px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 20px;
    }
    
    #string {
      height: 150px;
      width: 100%;
    }
    
    nav button.nav-button {
      padding: 15px 28px;
      font-size: 16px;
      border-radius: 8px;
      background-color: transparent;
      font-weight: 800;
      font-family: 'Poppins', sans-serif;
      border: none;
      position: relative;
      overflow: hidden;
      cursor: pointer;
    }
    
    nav button.nav-button h4 {
      position: relative;
      z-index: 1;
      font-weight: bold;
      font-size: 18px;
    }
    
    nav button.nav-button::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 0;
      transition: width 0.4s;
      z-index: 0;
    }
    
    nav button.nav-button.about-us h4 {
      color: orange;
    }
    
    nav button.nav-button.contact-us h4 {
      color: green;
    }
    
    nav button.nav-button.services h4 {
      color: rgb(19, 29, 220);
    }
    
    nav button.nav-button.about-us::before {
      background-color: orange;
      border-color: rgb(255, 115, 0);
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
    }
    
    nav button.nav-button.services::before {
      background-color: lightblue;
      border-color: blue;
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
    }
    
    nav button.nav-button.contact-us::before {
      background-color: green;
      border-color: rgb(1, 69, 1);
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
    }
    
    nav button.nav-button:hover::before {
      width: 100%;
    }
    
    nav button.nav-button.about-us:hover h4,
    nav button.nav-button.contact-us:hover h4 {
      color: white;
    }
    
    nav button.login-button {
      padding: 15px 28px;
      font-size: 16px;
      border-radius: 8px;
      background-color: transparent;
      font-weight: 600;
      border: 1px solid deepskyblue;
      cursor: pointer;
    }
    
    .orange {
      color: orange;
    }
    
    .blue {
      color: blue;
    }
    
    .green {
      color: green;
    }
    
    .center-part3 {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    
    .expand-button {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 150px;
      width: 150px;
      border-radius: 50%;
      background-color: #007bff;
      color: white;
      font-size: 16px;
      font-weight: bold;
      border: none;
      cursor: pointer;
      transition: all 0.3s ease;
    }
    
    .expand-button:hover {
      transform: scale(1.1);
    }
    
    .button-text {
      transition: inherit;
    }
    
    .Register {
      padding: 15px 30px;
      font-size: 18px;
      font-weight: bold;
      background-color: white;
      color: black;
      border: 2px solid orange;
      border-radius: 10%;
      cursor: pointer;
      transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
    }
    
    .Register:hover {
      transform: scale(1.2);
      background-color: orange;
      color: white;
    }
    
    .footer {
      text-align: center;
      padding: 8px;
      background-color: #f0f0f0;
    }
    nav
    {
      text-decoration: none;
    }